
<template>
<p id="yiyan"></p>
</template>



<script>
export default {
  name: "FirstWord",
  data: () => ({}),
};

//一言语句👇
fetch("https://v1.hitokoto.cn")
  .then((response) => response.json())
  .then((data) => {
    const hitokoto = document.getElementById("yiyan");
    hitokoto.href = "https://hitokoto.cn/?uuid=" + data.uuid;
    hitokoto.innerText = data.hitokoto;
  })
  .catch(console.error);
//一言语句👆

</script>


<style scoped>
#yiyan {
  height:28px;

  font-family: 微软雅黑;
  font-size: 20px;
  text-align: center;

